
* {
  font-family: Arial, Helvetica, sans-serif;

}

body { padding:10px;}

input {
  color: grey;
  font-size: 1.2rem;
  width:100%; 
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content:  center;
  border: 1px solid grey;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 5px;
  margin-right: 5px;
}

.card p {
  padding: 5px;
  text-align: center
}

.card img {
  max-height: 170px;
}